

const MountDate = Date.now()
export function getCurrentDate(Config) {
	var timePassed = (Config.isDev && Config.speedUpTime)
		? (Date.now() - MountDate) * Config.speedUpTime
		: Date.now() - MountDate;
	var now = (Config.isDev && Config.overrideDate)
		? new Date(Date.parse(Config.overrideDate) + timePassed)
		: new Date(Date.now());

	now.setSeconds(0);

	return now.valueOf();
}

export function getDateWithoutTime(fullDate) {
	var date = new Date(fullDate);
	date.setHours(0,0,0,0);
	return date.valueOf();
}

export function getTimeWithoutDate(fullDate) {

	var dateWithoutTime = getDateWithoutTime(fullDate);
	var time = new Date(fullDate).valueOf() - dateWithoutTime;
	return time;
}

export function getCurrentTime(Config) {
	var currentDate = getCurrentDate(Config);
	currentDate = new Date( currentDate );
	var minutes = currentDate.getMinutes();
	minutes = (minutes < 10) ? "0" + minutes : minutes;

	var hours = currentDate.getHours();
	CurrentAMPM = (hours < 12) ? "am" : "pm";
	hours = (hours > 12) ? hours - 12 : hours;
	hours = (hours === 0) ? 12 : hours;

	return hours + ":" + minutes;
}

export var CurrentAMPM = "";

export const ONE_DAY = 86400000;
export const ONE_MINUTE = 60000;


