import React, { Component, Fragment } from 'react';
import { TweenMax, Expo } from "gsap";
import Media from './utility/Media.js';
import Quote from './utility/Quote.js';
import Footer from './Footer.js';
import $ from 'jquery';

const ProductionsJSON = require('./../data/productions.json');
class TheatrePoster extends Component {

	state = {
		productionData: {},
		sortedSponsors: {},
		seasonSponsorsVisible: [],
		leadSponsorsVisible: [],
		productionSponsorsVisible: [],
		creditsVisible: []

	}

	constructor(props) {
		super(props);

		// 1. get the rest of production data using poster id
		for (var index in ProductionsJSON) {
			if (this.props.data.poster_id == ProductionsJSON[ index ].id) {
				this.state.productionData = ProductionsJSON[ index ];
				break;
			}
		}

		// 2. if production data doesn't exist, skip over slide
		if (!this.props.data.poster_id || $.isEmptyObject(this.state.productionData))
			this.slideTime = 0;

		this.state.sortedSponsors = this.parseSponsors( this.state.productionData.sponsors );

		setVisibleItems(this.state.seasonSponsorsVisible, this.state.sortedSponsors.season, 2);
		setVisibleItems(this.state.leadSponsorsVisible, this.state.sortedSponsors.lead, 3);
		setVisibleItems(this.state.productionSponsorsVisible, this.state.sortedSponsors.production, 3);
		setVisibleItems(this.state.creditsVisible, this.state.productionData.credits, 6);

		function setVisibleItems(stateObject, dataObject, maxNumber) {
			for (var i in dataObject) {
				if (i == maxNumber) return;
				stateObject.push({
					index: parseInt(i),
					data: dataObject[i]
				})
			}			
		}
	}

	parseSponsors = (allSponsors) => {
		var sponsorsSorted = {
			lead: [],
			production: [],
			season: []
		}
		
		for (var index in allSponsors) {
			switch (allSponsors[index].type) {
				case "Lead Sponsor":
					sponsorsSorted.lead.push(allSponsors[index]);
					break;
				case "Production Sponsor":	
					sponsorsSorted.production.push(allSponsors[index]);
					break;
				case "Season Sponsor":
					sponsorsSorted.season.push(allSponsors[index]);
					break;
				default:
					break;
			}
		}

		return sponsorsSorted;
	}

	componentDidUpdate(){
		this.props.slideLifecycle(this.enter, this.exit)
	}

	enter = () => {
		TweenMax.killTweensOf(Object.values(this.refs));


		TweenMax.set(this.refs.title, {alpha: 0, y: "15%"})
		TweenMax.to(this.refs.title, 2, {alpha: 1, y: "0%", delay: .5});

		TweenMax.set(this.refs.runtime, {alpha: 0, y: 30});
		TweenMax.set(this.refs.hashtag, {alpha: 0, y: 30});
		TweenMax.to(this.refs.runtime, 2, {alpha: 1, y: 0, delay: 2});
		TweenMax.to(this.refs.hashtag, 2, {alpha: 1, y: 0, delay: 2.2});

		TweenMax.set(this.refs.quote, {alpha: 0, y: 30});
		TweenMax.set(this.refs.byline, {alpha: 0, y: 30});
		TweenMax.to(this.refs.quote, 2, {alpha: 1, y: 0, delay: 3})
		TweenMax.to(this.refs.byline, 2, {alpha: 1, y: 0, delay: 3.2})

		TweenMax.set(this.refs.line1, {alpha: 0})
		TweenMax.to(this.refs.line1, 1, {alpha: 1, delay: 4})

		TweenMax.set($('.credit', this.refs.credits), {alpha: 0, y: 5});
		TweenMax.staggerTo($('.credit', this.refs.credits), 2, {alpha: 1, y: 0, delay: 4}, .2);
		
		TweenMax.set($('.sponsor-group', this.refs.allSponsors), {alpha: 0, y: 10});
		TweenMax.staggerTo($('.sponsor-group', this.refs.allSponsors), 2, {alpha: 1, y: 0, delay: 5}, .3);

		TweenMax.set(this.refs.line2, {alpha: 0})
		TweenMax.to(this.refs.line2, 1, {alpha: 1, delay: 6.3})

		TweenMax.set(this.refs.media.refs.caption, {alpha: 0});
		TweenMax.to(this.refs.media.refs.caption, 2, {alpha: 1, delay: 6.6})
	

		var _this = this;

		TweenMax.delayedCall(10, _this.rotateVisible, ["creditsVisible", this.state.productionData.credits, this.refs.credits, 6])
		TweenMax.delayedCall(10, _this.rotateVisible, ["seasonSponsorsVisible", this.state.sortedSponsors.season, this.refs.seasonSponsorLogos, 2])
		TweenMax.delayedCall(10, _this.rotateVisible, ["leadSponsorsVisible", this.state.sortedSponsors.lead, this.refs.sponsorLogos, 3])
		TweenMax.delayedCall(10, _this.rotateVisible, ["productionSponsorsVisible", this.state.sortedSponsors.production, this.refs.productionSponsorLogos, 3])
	}

	exit = () => {
		TweenMax.killTweensOf(Object.values(this.refs));
		TweenMax.killChildTweensOf(Object.values(this.refs));
		TweenMax.killTweensOf(this.refs.media.refs.caption);
		TweenMax.killDelayedCallsTo(this.rotateVisible);
	}

	rotateVisible = (stateVariable, allItems, ref, maxLength) => {
		
		// 1. if more items than visible, cycle through else, return
		var currentVisibleItems = this.state[ stateVariable ];

		if (allItems.length <= currentVisibleItems.length)
			return;

		// 2. for every visible item, replace with the next item
		var nextVisibleItems = [];
		var lastIndex = currentVisibleItems[ currentVisibleItems.length - 1].index;
		if (!allItems[lastIndex + 1]) lastIndex = -1;

		for (var i = 0; i < maxLength; i++) {
			lastIndex += 1;
			if (!allItems[lastIndex])
				break;
			nextVisibleItems.push({
				index: lastIndex,
				data: allItems[lastIndex]
			})	
					
		}

		// 3. fade out old logos
		var _this = this;
		TweenMax.to(ref, .5, {alpha: 0, onComplete: function() {

			// 4. and update state to update the logo
			// equivalent of this.setState({stateVariable: nextVisibleItems});
			var nextState = {};
			nextState[ stateVariable ] = nextVisibleItems;
			_this.setState( nextState );

			// 5. fade back and update again
			TweenMax.to(ref, .5, {alpha: 1});

		}});

		TweenMax.delayedCall(5, _this.rotateVisible, [stateVariable, allItems, ref, maxLength]);
	}

	createCredits = (credits) => {
		var creditsHTML = []
		for (var index in credits) {
			var id = "credit" + index;
			creditsHTML.push(
				<div className="credit" key={ id } >
					<span className="label">{ credits[index].data.label }</span>
					<span className="name">{ credits[index].data.name }</span>
				</div>	
			);	
		}
		return creditsHTML;
	}

	switchToColorLogo = (event) => {
		if (event.target.src === event.target.getAttribute('backup-src'))
			return;

		event.target.src = event.target.getAttribute('backup-src');

	}
	createLogos = (logos) => {
		var logosHTML = []
		for (var index in logos) {
			var id = "logo" + index;
			logosHTML.push(
				<div className="logo"key={ id }>
					<img
						src={ "http:" + logos[index].data.grayscale_logo }
						backup-src={ "http:" + logos[index].data.logo }
						alt={ logos[index].data.name }
						onError={ this.switchToColorLogo }
						/>
				</div>
			);	
		}
		return logosHTML;
	}

	render() {
		
		var quote = this.state.productionData.reviews && this.state.productionData.reviews[0]
			? (<div className="col-half">
				<div ref="quote" className="quote"><Quote rawText={ this.state.productionData.reviews[0].title } /></div>
				<div ref="byline" className="byline">{ this.state.productionData.reviews[0].attribution }</div>
			   </div> )
			: (<div className="col-half">
				<div ref="quote"></div>
				<div ref="byline"></div>
			   </div> )
		
		var row2visible = this.state.productionData.credits && this.state.productionData.credits[0]
			|| this.state.sortedSponsors.season.length > 0
			|| this.state.sortedSponsors.lead.length > 0
			|| this.state.sortedSponsors.production.length > 0;

		var background_video = ( this.state.productionData.sign_video && !this.state.productionData.sign_video.startsWith("http"))
			? "http:" + this.state.productionData.sign_video
			: this.state.productionData.sign_video;

		var background_image = ( this.state.productionData.sign_image_background && !this.state.productionData.sign_image_background.startsWith("http"))
			? "http:" + this.state.productionData.sign_image_background
			: this.state.productionData.sign_image_background;

		return (
			<Fragment>
				<div className="slide-content">
					<div ref="title" className={"title " + this.state.productionData.logo_position}>
						{ this.state.productionData.grayscale_logo && this.props.data.use_title == "no"
							? <img className="logo" src={ "http:" + this.state.productionData.grayscale_logo } alt=""/>
							: <h1 ref="title" className="text">{ this.state.productionData.title }</h1>
						}
					</div>
					<Media
						ref="media"
						video={ background_video }
						image={ background_image }
						caption={ this.state.productionData.sign_image_credit }
						slideLifecycle={ this.props.slideLifecycle }
						config={ this.props.config } />
					<div className="overlay"></div>
					<div className="poster-text">
						<div className="row main-text">
							{ this.props.data["quote-position"] == "left" ? quote : null}
							<div ref="runtime" className="col-half">
								<div className="playing">Playing</div>
								<div className="run-time" dangerouslySetInnerHTML={{__html: this.state.productionData.subtitle }}></div>
								<div ref="hashtag" className="hashtag">{ this.state.productionData.sign_promotext }</div>
							</div>
							{ this.props.data["quote-position"] != "left" ? quote : null}
						</div>
						<div ref="line1" className={"line" + (row2visible ? "" : " hidden")}></div>
						<div className={"row meta-data" + (this.state.productionData.sign_image_credit ? " has-caption" : "")}>
							<div ref="credits" className="col-half credits">
								{ this.createCredits(this.state.creditsVisible) }
							</div>
							<div ref="allSponsors" className="sponsors">
								{ (this.state.sortedSponsors.season.length > 0)
									? 	<div className="season-sponsors sponsor-group">
											<div className="label">Season Sponsors</div>
											<div className="logos">
												{ this.createLogos(this.state.seasonSponsorsVisible) }
											</div>
										</div>
									: null
								}
								{ (this.state.sortedSponsors.lead.length > 0)
									? 	<div className="lead-sponsors sponsor-group">
											<div className="label">Lead Sponsors</div>
											<div className="logos">
												{ this.createLogos(this.state.leadSponsorsVisible) }
											</div>
										</div>
									: null
								}
								{ (this.state.sortedSponsors.production.length > 0)
									? 	<div className="production-sponsors sponsor-group">
											<div className="label">Production Sponsors</div>
											<div className="logos">
												{ this.createLogos(this.state.productionSponsorsVisible) }
											</div>
										</div>
									: null
								}
							</div>
						</div>
						<div ref="line2" className="line"></div>
					</div>
				</div>
				<Footer text={ this.props.data.sign_footer_text }/>
			</Fragment>
		)
	}
}

export default TheatrePoster;


