import React, { Component, Fragment } from 'react';
import { TweenMax, Expo } from "gsap";
import Media from './utility/Media.js';
import Quote from './utility/Quote.js';
import SplitText from './../libs/SplitText.js';
import Footer from './Footer.js';
import $ from 'jquery';


class Storytelling extends Component {
	slideTime = this.props.data.duration ? parseInt(this.props.data.duration) : this.props.config.defaultSlideTime.liteInterpretive;

	componentDidMount(){
		// 1. set all text slides to the same height
		var allTextHeights = [];
		$(".text-slide", this.refs.textSlides).each(function() {
			allTextHeights.push($(this).outerHeight());
		})
		

		var maxTextHeight = Math.max(...allTextHeights);

		$(this.refs.textSlides).height(maxTextHeight);
	}

	componentDidUpdate(){
		this.props.slideLifecycle(this.enter, this.exit)
	}

	enter = () => {
		if (this.splitText)
			this.splitText.revert();

		TweenMax.killDelayedCallsTo(this.switchStorySlide);
		TweenMax.killTweensOf( Object.values(this.refs) );
		this.setStorySlides();
		
		// start slide switch from beginning
		var currentStorySlide = -1;
		this.switchStorySlide(currentStorySlide);	

		TweenMax.set($(".label", this.refs.textSlides), {alpha: 0, y: 10});
		TweenMax.to($(".label", this.refs.textSlides), 1, {alpha: 1, y: 0, delay: .5});	
	}

	exit = () => {
		// 1. kill switch slides when slide comes into view
		TweenMax.killDelayedCallsTo(this.switchStorySlide);
		TweenMax.to(this.refs.textSlides, .5, {alpha: 0});
					
	}

	revertSplitText = () => {
		this.splitText.revert();
	}

	switchStorySlide = (currentStorySlide) => {
		var _this = this;


		// 1. stop repeated call to switch story slide if next slide doesn't exist
		var nextStorySlide = currentStorySlide + 1;

		if (!this.canChangeImageSlide(nextStorySlide) && !this.canChangeTextSlide(nextStorySlide))
			return;

		// 2. hide current story slide
		if (currentStorySlide > -1)
			this.exitStorySlide(currentStorySlide);


		// 3. show next slide
		this.enterStorySlide(nextStorySlide);

		// 4. change next slide to current slide and switch slide again
		var storySlideTime = this.slideTime / Math.max(this.props.data.highlights.length, this.props.data.images.length);
		TweenMax.delayedCall(storySlideTime, _this.switchStorySlide, [nextStorySlide]);
	}

	setStorySlides = () => {
		TweenMax.set(Object.values( this.refs ), {alpha: 0});
		TweenMax.set(this.refs.textSlides, {alpha: 1});
		TweenMax.set(this.refs.image0.refs.component, {alpha: 1});
		if (this.refs.image1)
			TweenMax.set(this.refs.image1.refs.component, {alpha: 0});
	}

	exitStorySlide = (currentStorySlide) => {
		if (this.canChangeImageSlide(currentStorySlide + 1)) {
			TweenMax.to(this.refs["image" + currentStorySlide].refs.component, 0, {alpha: 0, delay: 3});
			TweenMax.to(this.refs["image" + currentStorySlide].refs.caption, 3, {alpha: 0});
		}

		if (this.canChangeTextSlide(currentStorySlide + 1)) {
			TweenMax.to(this.refs["quote"+ currentStorySlide], 1, {alpha: 0});

			//if bylines are different, hide this byline
			if ( this.bylinesAreDifferent(this.props.data.highlights, currentStorySlide) )
				TweenMax.to(this.refs["byline"+ currentStorySlide], 1, {alpha: 0});
		}
	}

	enterStorySlide = (nextStorySlide) => {
		if (this.canChangeImageSlide(nextStorySlide)) {
			TweenMax.to(this.refs["image" + nextStorySlide].refs.component, 3, {alpha: 1, delay: 0});
			TweenMax.set(this.refs["image" + nextStorySlide].refs.caption, {alpha: 0});
			TweenMax.to(this.refs["image" + nextStorySlide].refs.caption, 5, {alpha: 1, delay: 4});
		
			if (this.props.data.artifact == "true") {
				TweenMax.set(this.refs["image" + nextStorySlide].refs.image, {alpha: 0, y: 100});
				TweenMax.to(this.refs["image" + nextStorySlide].refs.image, 5, {alpha: 1, y: 0, delay: .8});
			}
		}

		if (this.canChangeTextSlide(nextStorySlide)) {
			TweenMax.set(this.refs["quote"+ nextStorySlide], {alpha: 1});

			this.splitText = new SplitText(this.refs["quote"+ nextStorySlide], {type: "lines words"});
			TweenMax.set(this.splitText.words, {alpha: 0, y: "20%"});
			TweenMax.staggerTo(this.splitText.words, 2.7, {
				alpha: 1,
				y: "0%",
				ease: Expo.easeOut,
				delay: 1
			}, 0.17);

			TweenMax.to(this.refs["byline"+ nextStorySlide], 1, { alpha: 1, delay: 3.8 });
		}
	}

	canChangeImageSlide = (slideIndex) => {
		return this.refs["image" + slideIndex];
	}

	canChangeTextSlide = (slideIndex) => {
		return this.refs["quote" + slideIndex] || this.refs["byline" + slideIndex]
	}

	bylinesAreDifferent = (highlights, currentIndex) => {
		
		if (!highlights[currentIndex + 1]) return false;

		var byline1_current = highlights[currentIndex].byline_1;
		var byline1_next = highlights[currentIndex + 1].byline_1;

		var byline2_current = highlights[currentIndex].byline_2;
		var byline2_next = highlights[currentIndex + 1].byline_2;

		var bylinesAreSame = (byline1_current === byline1_next) && (byline2_current === byline2_next);
		return !bylinesAreSame;
	}

	/* Render Functions */

	createImageSlides = (imageData) => {
		var imageSlidesHTML = [];
		var animationTime = this.slideTime / this.props.data.images.length;
		for (var index in imageData) {
			var key = "image" + index;
			imageSlidesHTML.push(
				<Media
					ref={ key }
					key={ key }
					image={ imageData[index].image.url }
					caption={ imageData[index].image.image_caption }
					credit={ imageData[index].image.image_credit }
					border={ (this.props.data.artifact != "true") }
					animation={ (this.props.data.artifact != "true") ? imageData[index].image.animation : undefined }
					animationTime={ animationTime }
					animationDelay={ animationTime * index }
					slideLifecycle={ this.props.slideLifecycle }
					config={ this.props.config }
					nocrop={ (this.props.data.artifact == "true") ? true : undefined }/>
			);
		}
		return imageSlidesHTML;
	}

	createTextSlides = (textData) => {
		var textSlidesHTML = [];

		for (var index in textData) {
			var key = "text" + index;
			var hasByline = textData[index].byline_2 || textData[index].byline_1;
			textSlidesHTML.push(
				<div key={ key } className="text-slide">
					<div className={"label" + (!this.props.data.label ? " hidden" : "")}>{ this.props.data.label }</div>
					{ /*<h1 ref={"quote" + index} dangerouslySetInnerHTML={{__html: textData[index].highlights}}></h1> */}
					<h1 ref={"quote" + index}><Quote rawText={ textData[index].highlights } /></h1>
					<div ref={"byline" + index} className={"byline" + (hasByline ? "" : " hidden")}>
						<span className="name">{ textData[index].byline_1 }</span> 
						<span className="role">{ textData[index].byline_2 }</span>
					</div>
				</div>
			);
		}
		return textSlidesHTML;		
	}

	render() {

		var classes = "";
		for (var index in this.props.data.images) {
			if (this.props.data.images[ index ].image.image_credit || this.props.data.images[ index ].image.image_caption)
				classes = "has-caption";
		}

		//classes += (this.props.data.label) ? " has-label" : "";
		classes += (this.props.data.text_position == "bottom") ? " inverse-text-position" : "";
		return (
			<Fragment>
				<div className="slide-content">
					<div className={"image-wrapper" + (this.props.data.artifact == "true" ? " artifact" : "") }>
						{ this.createImageSlides(this.props.data.images) }
					</div>
					<div ref="textSlides" className={ "text-wrapper " + classes }>
						<span className={"label" + (this.props.data.highlights[0] ? " hidden" : "")}>{ this.props.data.label }</span>
						{ this.createTextSlides(this.props.data.highlights) }
					</div>
				</div>
				<Footer text={ this.props.data.footer_text } />
			</Fragment>
		)
	}
}

export default Storytelling;


