import React, { Component } from 'react';
import { TweenMax } from 'gsap';
import $ from 'jquery';



class Quote extends Component {

 	constructor(props) {
 		super(props);
 		var html = $.parseHTML( props.rawText );
 		this.text = $( html ).text();
 		var firstChar = this.text.charAt(0);
 		this.hasQuote = firstChar == '"' || firstChar == '\u201c';
 	}

	render() {

		return (
			<div
				ref="component"
				className={ this.hasQuote ? "hanging-quote" : ""}
				dangerouslySetInnerHTML={{__html: this.props.rawText }}>
			</div>
		)
	}
}

export default Quote;


