import React, { Component, Fragment } from 'react';
import { TweenMax } from 'gsap'
import Media from './utility/Media.js'
import Footer from './Footer.js'


class MediaSlide extends Component {

 	componentDidMount() {
 		
 		// 1. set slide time to length of video
 		var video = this.refs.media.refs.video
 		if (video) {
 			var _this = this;
 			video.addEventListener('loadedmetadata', this.setSlideTimeToVideoLength)
		 }
 	}

 	setSlideTimeToVideoLength = (element) => {
 		console.log("metadataloaded", element.target.duration)
 		this.props.setSlideTime(element.target.duration)

 	}

	render() {
		
		return (
			<Fragment>
				<div className="slide-content">
				{	this.props.data.image ?
					<Media
						ref="media"
						image={ this.props.data.image.url }
						caption={ this.props.data.image.image_caption }
						credit={ this.props.data.image.image_credit }
						slideLifecycle={ this.props.slideLifecycle }
						config={ this.props.config }
					/> 
				:
					<Media
						ref="media"
						video={ this.props.data.video.url }
						caption={ this.props.data.video.video_caption }
						credit={ this.props.data.video.video_credit }
						slideLifecycle={ this.props.slideLifecycle }
						config={ this.props.config }
					/>  
				}
					
				</div>
				<Footer text={ this.props.data.footer_text }></Footer>
			</Fragment>
		)
	}
}

export default MediaSlide;


