import React, { Component, Fragment } from 'react';
import Schedule from './schedule/Schedule.js';
import { TweenMax, Expo } from "gsap";



class FullSchedule extends Component {

	state = {
		currentSchedule: Object.keys(this.props.schedule)[0]
	}
	minHeightNeeded = 190;

	componentDidMount(){
		//when your component loads 
		//instantiating states
		for (var ref in this.refs) {
			TweenMax.set(this.refs[ ref ].refs.clock, {alpha: 0});
		}
		TweenMax.set(this.refs["schedule" + this.state.currentSchedule].refs.clock, {alpha: 1});
	}

	componentDidUpdate(){
		//this.props.slideLifecycle(this.enter, this.exit);
		for (var ref in this.refs) {
			TweenMax.killTweensOf(this.refs[ ref ].refs.clock);
			if (ref != ("schedule" + this.state.currentSchedule))
				TweenMax.set(this.refs[ ref ].refs.clock, {alpha: 0});
		}

		TweenMax.to(this.refs["schedule" + this.state.currentSchedule].refs.clock, 1, {alpha: 1});
		this.checkForRoom(0);
	}
 
 	enter = () => {

 	}


	exit = () => {

	}


	hide = (scheduleEntryRefs) => {
		TweenMax.killChildTweensOf(scheduleEntryRefs.component);
		TweenMax.to(scheduleEntryRefs.alert, 1, {alpha: 0});
		TweenMax.to(scheduleEntryRefs.marker, 1, {x: "-100%", ease: Expo.easeIn});
		TweenMax.to(scheduleEntryRefs.content, 1, {y: 0, alpha: 0, delay: .5});
		TweenMax.to(scheduleEntryRefs.component, 1, {alpha: 0, delay: 1.5, height: 0, borderTop: "none"});

		this.checkForRoom(2);
	}

	set = (scheduleEntryRefs) => {
		TweenMax.set(scheduleEntryRefs.component, {height: 105});

	}
	queue = (scheduleEntryRefs) => {
		TweenMax.killTweensOf(scheduleEntryRefs.component);
		TweenMax.killChildTweensOf(scheduleEntryRefs.component);
		
		TweenMax.to(scheduleEntryRefs.content, 1, {y: 7, alpha: 1})
		TweenMax.to(scheduleEntryRefs.component, 1, {height: 190});
		TweenMax.to(scheduleEntryRefs.alert, 1, {alpha: 1, delay: 1});
		TweenMax.to(scheduleEntryRefs.marker, 1, {x: "0%", delay: .5, ease: Expo.easeIn});

		this.checkForRoom(1.5);
	}

	switch = (dayValueOfCaller) => {
		// 1. only call from currently displayed schedule
		if (dayValueOfCaller != this.state.currentSchedule)
			return;

		// 2. hide current schedule
		var currentSchedule = this.refs["schedule" + this.state.currentSchedule]
		var nextScheduleIndex = this.getNextSchedule();
		var nextSchedule = this.refs["schedule" + nextScheduleIndex];

		TweenMax.killTweensOf(currentSchedule.refs.component);
		TweenMax.to(currentSchedule.refs.component, 1, {alpha: 0});
		TweenMax.to(currentSchedule.refs.component, 1.5, {height: 0, delay: .8, ease: Expo.easeOut});


		// 3. show next schedule's time
		// TweenMax.killTweensOf(nextSchedule.refs.clock)
		// TweenMax.set(nextSchedule.refs.clock, {alpha: 0});
		TweenMax.to(nextSchedule.refs.clock, 1.5, {alpha: 1, delay: 2.3});

		// 4. switch the current schedule to tomorrow
		this.setState({ currentSchedule: nextScheduleIndex })
	}

	checkForRoom = (delay) => {
		var currentSchedule = this.refs["schedule" + this.state.currentSchedule].refs.component
		var nextScheduleDayValue = this.getNextSchedule();
		var nextSchedule = this.refs["schedule" + nextScheduleDayValue].refs.component
		var minHeightNeeded = this.minHeightNeeded;
		TweenMax.delayedCall(delay, function() {
			if (document.body.scrollHeight - currentSchedule.offsetHeight > minHeightNeeded)
				TweenMax.to(nextSchedule, 1, {alpha: 1});
			else {
				TweenMax.set(nextSchedule, {alpha: 0});
			}
		})

	}

	getNextSchedule = () => {
		for (var dayValue in this.props.schedule) {
			if (dayValue > this.state.currentSchedule)
				return dayValue;
		}

	}

	render() {
		var functions = {
			hide: this.hide,
			queue: this.queue,
			set: this.set,
			switch: this.switch
		}

		var schedulesHTML = [];
		for (var dayValue in this.props.schedule) {
			schedulesHTML.push(
				<Schedule
					key={ dayValue }
					dayValue={ dayValue }
					ref={ "schedule" + dayValue }
					isCurrent={ dayValue == this.state.currentSchedule }
					schedule={ this.props.schedule[ dayValue ] }
					slideLifecycle={ this.props.slideLifecycle }
					functions={ functions }
					config={ this.props.config }
				/>
			);
		}

		return (
			<div className="slide-content">
				{ schedulesHTML }
			</div>
		)

	}
}

export default FullSchedule;


