import React, { Component, Fragment } from 'react';
import Media from './utility/Media.js';
import Footer from './Footer.js';
import { TweenMax } from "gsap";



class LiveFeed extends Component {

	componentDidUpdate() {
		this.props.slideLifecycle(this.enter, this.exit);
	}

	enter = () => {

		TweenMax.set(this.refs.logo, {alpha: 0});
		TweenMax.set(this.refs.caption, {alpha: 0, x: 50});
		TweenMax.to(this.refs.logo, 3, {alpha: 1, delay: 1});
		TweenMax.to(this.refs.caption, 4, {alpha: 1, x: 0, delay: 1.8});

	}

	exit = () => {

		TweenMax.killTweensOf(Object.values(this.refs));
	} 

	render() {
		
		return (
			<Fragment>
				<div className="slide-content">
					<div className="blur">
						<Media image={ this.props.data.image } />
					</div>
					<div className="overlay">
						<Media video={ this.props.data.video } nocrop config={ this.props.config } slideLifecycle={ this.props.slideLifecycle }/>
						<div className="text">
							<div ref="logo" className="logo">
								{ this.props.data.logo 
									? <img src={ this.props.data.logo } />
									: <object data={'./logo.svg'} type="image/svg+xml">Logo</object>
								}
							</div>
							<div ref="caption" className="caption">{ this.props.data.title } { this.props.data.caption }</div>
						</div>
					</div>
				</div>
			</Fragment>
		)
	}
}

export default LiveFeed;


