import React, { Component } from 'react';
import { TweenMax } from 'gsap';
import $ from 'jquery';



class Controls extends Component {


	shouldComponentUpdate(nextProps, nextState) {
		return (this.props.currentPlaylistID !== nextProps.currentPlaylistID)
			|| this.props.isVisible !== nextProps.isVisible;
	}

	changePlaylistFromSelect = (event) => {
		this.props.changePlaylist(event.target.value);
	}

	render() {

		var options = [];

		for (var playlist in this.props.allPlaylists) {

			options.push(
				<option
					key={ playlist }
					value={ playlist }
					selected={ playlist == this.props.currentPlaylistID ? "selected" : undefined } >
					{ this.props.allPlaylists[ playlist ].title }
				</option>
			);
		}

		return (
			<div id="controls" className={ this.props.isVisible ? "" : "hidden" }>
				<label>Choose Playlist: </label>
				<select onChange={ this.changePlaylistFromSelect }>
					{ options }
				</select>
			</div>
		)
	}
}

export default Controls;


