import React, { Component, Fragment } from 'react';
import { TweenMax, Expo } from "gsap";
import SplitText from './../libs/SplitText.js';
import Media from './utility/Media.js';
import Quote from './utility/Quote.js';
import Footer from './Footer.js';


class Announcement extends Component {

	componentDidUpdate() {

		this.props.slideLifecycle(this.enter, this.exit);
	}

	paragraphs = [];
	enter = () => {
		if (this.splitText)
			this.splitText.revert();

		TweenMax.set(this.refs.label, {y: "-100%"});
		TweenMax.to(this.refs.label, 1, {y: "0%", delay: 1, ease: Expo.easeOut});

		this.splitText = new SplitText(this.refs.header.refs.component, {type: "lines words"});
		TweenMax.set(this.splitText.lines, {alpha: 0, y: "30%"});
		TweenMax.staggerTo(this.splitText.lines, 5, {
			alpha: 1,
			y: "0%",
			ease: Expo.easeOut,
			delay: 1.8
		}, 0.18);

		this.paragraphs = this.refs.description.getElementsByTagName("P");
		
		TweenMax.set(this.refs.border, {width: "100%", alpha: 0, y: 30})
		TweenMax.to(this.refs.border, 2, {width: "100%", alpha: .2, y: 0, delay: 2.8})

		TweenMax.set(this.paragraphs, {alpha: 0, y: 50});
		for (var i = 0; i < this.paragraphs.length; i++) {
			var delay =3.8 + (i * 1.4);
			TweenMax.to(this.paragraphs[i], 3, {alpha: 1, y:0, delay: delay})
		}
	}

	exit = () => {
		TweenMax.killChildTweensOf(this.refs.description);
		TweenMax.killTweensOf(this.refs.border);
		TweenMax.killTweensOf(this.refs.label);
	}
 
	render() {
		

		return (
			<Fragment>
				<div className={"slide-content " + this.props.data["background-color"] }>
					{ this.props.data.background_image.url
					  ? <Media
					  		image={ this.props.data.background_image.url }
					  		caption={ this.props.data.background_image.image_caption }
					  		credit={ this.props.data.background_image.image_credit }
					  		config={ this.props.config }
					  		/>
					  : null
					}
					
					<div className="overlay-wrapper">
						<div ref="label" className={ "label" + (this.props.data.label ? "" : " hidden") }>{ this.props.data.label }</div>
						<div className="text-wrapper">
							<h1>
								<Quote ref="header" rawText={this.props.data.announcement_headline} />
							</h1>
							<div className={ this.props.data.announcement_text ? "description-wrapper" : "hidden" }>
								<div ref="border" className="border"></div>
								<div ref="description" className="description">
									<Quote rawText={this.props.data.announcement_text} />
								</div>
							</div>
						</div>
					</div>
				</div>
				<Footer isLight={ !this.props.data.background_image.url } text={ this.props.data.footer_text } />
			</Fragment>
		)
	}
}

export default Announcement;


