import React, { Component, Fragment } from 'react';
import Schedule from './schedule/Schedule.js';
import ScheduleEntry from './schedule/ScheduleEntry.js';
import Media from './utility/Media.js'
import Footer from './Footer.js';
import { getCurrentDate, ONE_MINUTE } from './../libs/utility.js'
import { TweenMax, Expo } from "gsap";
import $ from 'jquery';



class Dashboard extends Component {
	slideTime = this.props.data.duration ? parseInt(this.props.data.duration) : this.props.config.defaultSlideTime.schedule;
	noAlert = true;
	maxEntriesVisible = 5;
	state = {
		alertTime: "",
		currentSchedule: Object.keys(this.props.schedule)[0]
	}


	componentDidUpdate(){
		this.props.slideLifecycle(this.enter, this.exit);
	}
 
 	enter = () => {
 		TweenMax.killTweensOf(this.refs.alert);
 		if (this.noAlert)
 			this.setState({alertTime: ""})

		TweenMax.set(this.refs.alert, {y: "-100%"});

		var _this = this;

		if (this.refs.map)
			TweenMax.set(this.refs.map, {scale: 1.2})
		TweenMax.delayedCall(2, function() {
			_this.updateAlert(_this.state.alertTime);
		})
		

		var currentSchedule = this.refs["schedule" + this.state.currentSchedule];
		var delay = .8;
		currentSchedule.refs.component.classList.add("ani-in");

		TweenMax.delayedCall(1.8, function() {
			currentSchedule.refs.component.classList.remove("ani-in");
		})

		var currentDate = getCurrentDate(this.props.config);

		for (var timestamp in currentSchedule.props.schedule) {
			timestamp = parseInt(timestamp);

			// 1. only enter in times that are visible
			if (timestamp >= (currentDate - (ONE_MINUTE * 15))) {
				var entry = currentSchedule.refs["entry" + timestamp].refs.content
				TweenMax.set(entry, { alpha: 0, y: 30 });
				TweenMax.to(entry, 2, {
					alpha: 1,
					y: 0,
					delay: delay,
					ease: Expo.easeOut
				});
				delay += .2;
			}
		}
	
 	}

	exit = () => {
		TweenMax.killDelayedCallsTo(this.updateAlert);
		TweenMax.killTweensOf(Object.values(this.refs));
		var currentSchedule = this.refs["schedule" + this.state.currentSchedule];

		for (var timestamp in currentSchedule.props.schedule) {
			TweenMax.killTweensOf(currentSchedule.refs["entry" + timestamp].refs.content);

		}
	}

	fadeUnseenEntries = (currentScheduleEntry) => {

		var currentScheduleEntry = $(currentScheduleEntry);
		var index = 0;
		while (currentScheduleEntry.length > 0) {
			if (index < this.maxEntriesVisible) {
				TweenMax.to(currentScheduleEntry, 1, {alpha: 1})

				if (index == this.maxEntriesVisible - 1)
					$(currentScheduleEntry).addClass("last-visible")
				else
					$(currentScheduleEntry).removeClass("last-visible")
			}
			else {
				TweenMax.to(currentScheduleEntry, 1, {alpha: 0})
			}


			currentScheduleEntry = $(currentScheduleEntry).next();
			index++;
		}

	}

	hide = (scheduleEntryRefs) => {
		var _this = this;
		TweenMax.killChildTweensOf(scheduleEntryRefs.component);
		TweenMax.to(scheduleEntryRefs.marker, 1, {x: "-100%", ease: Expo.easeIn});
		TweenMax.to(scheduleEntryRefs.component, 1, {alpha: 0, delay: 1, height: 0, borderTop: "none",
			onComplete: function() {
				//scheduleEntryRefs.component.remove();
				_this.fadeUnseenEntries($(scheduleEntryRefs.component).next());
			}
		});

		// if no other schedule entry is queuing
		if (this.state.alertTime === scheduleEntryRefs.time.textContent) {
			// remove alert
			this.updateAlert("");

		}
	}

	set = (scheduleEntryRefs) => {
		TweenMax.set(scheduleEntryRefs.component, {height: 140});	
	}

	queue = (scheduleEntryRefs) => {
		TweenMax.killTweensOf(scheduleEntryRefs.component);

		TweenMax.to(scheduleEntryRefs.component, 1, {height: 140});
		TweenMax.to(scheduleEntryRefs.marker, 1, {x: "0%", ease: Expo.easeIn});

		this.updateAlert( scheduleEntryRefs.time.textContent )
	}

	switch = (dayValueOfCaller) => {

		// 1. only switch from currently displayed schedule
		if (dayValueOfCaller != this.state.currentSchedule)
			return;

		// 2. hide current schedule
		var currentSchedule = this.refs["schedule" + this.state.currentSchedule].refs.component;
		var nextScheduleDayValue = this.getNextSchedule();
		TweenMax.to(currentSchedule, 1, {alpha: 0});
		TweenMax.to(currentSchedule, 2, {height: 0, ease: Expo.easeOut});

		// 3. remove alert since this will happen after the day is done
		TweenMax.to(this.refs.alert, 1, {y: "-100%"});

		// 4. switch current schedule to tomorrow
		this.setState({ currentSchedule: nextScheduleDayValue })
	}

	updateAlert = (formattedTime) => {
		TweenMax.killTweensOf(this.refs.alert);
		TweenMax.killTweensOf(this.refs.map);
		TweenMax.killTweensOf(this.refs.alertTime);
 
		var alertTime = formattedTime;

		// 2. if there's a queuing entry, show alert and update time
		if (alertTime != "") {
			this.noAlert = false;
			this.setState({ alertTime: alertTime });
			TweenMax.to(this.refs.alert, 1.5, {y: "0%", ease: Expo.easeOut, delay: 1});

			// and scale down the default map if used
			if (this.refs.map)
				TweenMax.to(this.refs.map, 2, {scale: 1.1})
		}
		// 3. or else hide the whole alert
		else {
			var _this = this;
			this.noAlert = true;
			TweenMax.to(this.refs.alert, 1.5, {y: "-100%", ease: Expo.easeIn, onComplete: function() {
				_this.setState({ alertTime: "" });
			}});

			// and scale up the default map if used
			if (this.refs.map)
				TweenMax.to(this.refs.map, 2, {scale: 1.2, delay: 1.4})
		}
	}

	getNextSchedule = () => {
		for (var dayValue in this.props.schedule) {
			if (dayValue > this.state.currentSchedule)
				return dayValue;
		}

	}

	render() {

		var functions = {
			hide: this.hide,
			set: this.set,
			queue: this.queue,
			switch: this.switch,
			fadeUnseenEntries: this.fadeUnseenEntries

		}

		var schedulesHTML = [];

		for (var dayValue in this.props.schedule) {
			
			schedulesHTML.push(
				<Schedule
					key={ dayValue }
					dayValue={ dayValue }
					ref={ "schedule" + dayValue }
					isCurrent={ dayValue == this.state.currentSchedule }
					schedule={ this.props.schedule[ dayValue ] }
					functions={ functions }
					slideLifecycle={ this.props.slideLifecycle }
					config={ this.props.config }
				/>
			);
		}

		return (
			<Fragment>
				<div className="slide-content">
					<div className="image-wrapper">

						{ this.props.data.background_image.url
							? 	<Media
									image={ this.props.data.background_image.url }
									caption={ this.props.data.background_image.image_caption }
									credit={ this.props.data.background_image.image_credit }
									config={ this.props.config }
									animation="zoom-in"
									animationTime={ this.slideTime }
									slideLifecycle={ this.props.slideLifecycle }/>
							:	<div ref="map" className="map">
									<Media 
										image={ this.props.config.mapDefault }
										caption=" "
										config={ this.props.config }/>
								</div>

						}
						<div ref="alert" className={"alert"}>
							<h1>NOW IN LINE: <span ref="alertTime">{ this.state.alertTime }</span> ticketholders.</h1>
						</div>
					</div>
					<div className="schedule-wrapper">
						{ schedulesHTML }
					</div>
				</div>
				<Footer isLight={ true } text={ this.props.data.footer_text }/>
			</Fragment>
		)

	}
}

export default Dashboard;


