import React, { Component } from 'react';
import Dashboard from './Dashboard.js';
import MediaSlide from './MediaSlide.js'
import Announcement from './Announcement.js';
import Storytelling from './Storytelling.js';
import TheatrePoster from './TheatrePoster.js';
import FullSchedule from './FullSchedule.js';
import LiveFeed from './LiveFeed.js';

import { TweenMax } from "gsap";



class Slide extends Component {

	state = {
		isCurrentSlide: false,
		shouldEnter: false,
		shouldExit: false,
		didEnter: false,
		didExit: false		
	}

	slideTime = 0; //set to zero so its skips over slides until slide time is set

	componentDidMount(){
		//when your component loads 
		//instantiating states
		this.slideTime = this.props.data.duration ? parseInt(this.props.data.duration) : this.slideTime;
		TweenMax.set(this.refs.component, { alpha: 0, zIndex: 0 })

		this.setState({
			didEnter: false,
			didExit: false
		});
	}

	componentWillReceiveProps(nextProps) {
		// 1. before re-render, allow exit and enter functions when current slide changes

		var willBeCurrentSlide = (nextProps.currentSlideIndex === this.props.slideIndex)
		var willChangePlaylists = (nextProps.currentPlaylistID !== this.props.currentPlaylistID)
		
		this.setState({
			shouldExit: this.state.isCurrentSlide && !willBeCurrentSlide,
			shouldEnter: willBeCurrentSlide && (!this.state.isCurrentSlide || willChangePlaylists),
			isCurrentSlide: willBeCurrentSlide
		})
	}

	componentDidUpdate(){
		this.slideLifecycle(this.enter, this.exit)

	}

	enter = () => {
		// 1. kill existing tweens and ani in
		TweenMax.killTweensOf(this.refs.component);
		TweenMax.set(this.refs.component, {zIndex: 5});
		TweenMax.to(this.refs.component, 1, { alpha: 1 });	
		
		// 2. mark that enter occurred
		this.setState({
			didEnter: true,
			didExit: false
		});

	}

	exit = () => {
		// 1. kill existing tweens and ani out
		TweenMax.killTweensOf(this.refs.component);
		TweenMax.set(this.refs.component, {zIndex: 0, alpha: 1});
		TweenMax.to(this.refs.component, 0, {alpha: 0, zIndex: -1, delay: 1});

		// 2. mark that exit occurred
		this.setState({
			didExit: true,
			didEnter: false
		});		
	}

	setSlideTime = (duration) => {
		this.slideTime = duration;
	}

	slideLifecycle = (enter, exit) => {
		// this function is available in child slide component
		// based on slide state, run exit and enter functions located in the child component
		// enter and exit only run once thanks to didExit and didEnter
		
		if (this.state.shouldExit && !this.state.didExit) {
			exit();
		}
		else if (this.state.shouldEnter && !this.state.didEnter)
			enter();
	}
 
	getTag = (slideType) => {

		var tagMap = {
			"still_image": MediaSlide,
			"schedule_item": (this.props.data.schedule_type == "full") ? FullSchedule : Dashboard,
			"announcement": Announcement,
			"lite_interpretive": Storytelling,
			"theatre_event_poster": TheatrePoster,
			"pre_rendered_video": MediaSlide
		}

		return tagMap[ slideType ];

	}

	getClass = (slideType) => {
		var classMap = {
			"schedule_item": (this.props.data.schedule_type == "full") ? "full-schedule" : "dashboard",
			"still_image": "static-image",
			"announcement": "announcement",
			"lite_interpretive": "storytelling",
			"theatre_event_poster": "theatre-poster",
			"pre_rendered_video": "video"
		}

		return classMap[ slideType ];
	}

	render() {
		var Tag = this.getTag(this.props.data.type);
		var slideClass = this.getClass( this.props.data.type );
		return (
			<div ref="component" className={"slide " + slideClass }>
				<Tag
					ref="childSlide"
					config={ this.props.config }
					data={ this.props.data }
					slideLifecycle={ this.slideLifecycle }
					setSlideTime={ this.setSlideTime }
					schedule={ Tag === Dashboard || Tag === FullSchedule ? this.props.schedule : undefined}
				/>
			</div>
		)
	}
}

export default Slide;


