import React, { Component } from 'react';
import { TweenMax } from "gsap";
import $ from 'jquery';


class ScheduleEntry extends Component {
	state = {
		didPass: false,
		didQueue: false,
		status: ""
	}

	componentDidMount() {
		TweenMax.set(this.refs.alert, {alpha: 0});
		TweenMax.set(this.refs.marker, {x:"-100%"});

		this.props.functions.set( this.refs );
		
		this.setState({ status: this.getStatus() })
	}

	shouldComponentUpdate(nextProps) {
		
		// only update if did queue and did pass changed
		return (this.props.isQueuing != nextProps.didQueue || this.props.hasPassed != nextProps.hasPassed || nextProps.sold_out != this.props.sold_out || nextProps.low_availability != this.props.low_availability);
	}
	componentWillUpdate(nextProps){
		var _this = this
		
		if(nextProps.sold_out != this.props.sold_out || nextProps.low_availability != this.props.low_availability){
			console.log("UPDATING")

			$(this.refs.component).addClass("status-change");

			TweenMax.to(this.refs.status, .5, {autoAlpha:0, delay: 1, onComplete:function(){
				_this.setState({ status: _this.getStatus() })
			}})
			TweenMax.to(this.refs.status, .5, {autoAlpha:1, delay: 1.5});

			TweenMax.delayedCall(10, function() {
				$(_this.refs.component).removeClass("status-change");
			})

		}
	}
	componentDidUpdate(){

		if (this.props.hasPassed && !this.state.didPass) {
			this.props.functions.hide(this.refs);
			this.setState({ didPass: true })
		}

		if (this.props.isQueuing && !this.state.didQueue) {
			this.props.functions.queue(this.refs);
			this.setState({ didQueue: true })
		}

	
	}
	getStatus(){
		var status = "Tickets Available";
		if (this.props.data.sold_out || this.props.hasPassed) status = "Sold Out";
		else if (this.props.data.low_availability) status = "Low Availability";
		return status;
	}
	render() {

		var classes = "schedule-entry";
		classes += this.props.isQueuing ? " is-queuing" : "";
		classes += this.props.hasPassed ? " has-passed" : "";

		var tags = [];
		for (var index in this.props.data.attractions) {
			var tag = this.props.data.attractions[ index ];
			tags.push(
				<span key={ index } className={"tag " + tag.color + " " + tag.state}>{ tag.name }</span>
			);
		}

		return (

			<li ref="component" className={ classes }>
				<div ref="marker" className="marker"></div>
				<div ref="content" className="content">
					<div className="column-wrapper">
						<div className="column time"><h2 ref="time">{ this.props.data.time_formatted.replace("m", ".m.") }</h2></div>
						{ this.props.data.title === "Historic Site Visit"
							? <div className="column main tags">{ tags }</div>
							: <div className="column main">
								<h2 className="title">{ this.props.data.title }</h2>
								{/*<div className="tags">
									<span className={"tag grey" + (!this.props.data.price ? " hidden" : "")}>{ "Price: " + this.props.data.price }</span>
									<span className={"tag grey" + (!this.props.data.duration ? " hidden" : "")}>{ "Duration: " + this.props.data.duration }</span>
								</div>*/}
							  </div>
						}
						<div className={"column status" + (this.props.sold_out ? " sold-out" : "")}>
							<span ref="status">{ this.state.status }</span>
						</div>
					</div>
					<div ref="alert" className="alert"><span>If you have a ticket for this time, please line up now.</span></div>
				</div>
			</li>

		)

	}
}

export default ScheduleEntry;


