import React, { Component } from 'react';
import ScheduleEntry from './ScheduleEntry.js';
import { TweenMax, Power0 } from "gsap";
import { getCurrentTime, getCurrentDate, getDateWithoutTime, CurrentAMPM, ONE_DAY, ONE_MINUTE } from './../../libs/utility.js'
import $ from 'jquery';



class Schedule extends Component {
	state = {
		currentTime: null,
		lastEntry: null,
		allTimestamps: [],
		isScrolling: false
	}

	isToday = false;
	isTomorrow = false;
	isFutureDay = false;

	dateName = "";

	weekdays = [ "Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday" ];
	scheduleDate = 0;

	constructor(props) {
		super(props);
		this.setDateVars();
	}
	setDateVars() {
		this.scheduleDate = getDateWithoutTime( parseInt(Object.keys(this.props.schedule)[0]) );
		var today = getDateWithoutTime( getCurrentDate(this.props.config) );

		// 1. get the first date in the schedule
		var month = new Date(this.scheduleDate).getMonth() + 1;
		var date = new Date(this.scheduleDate).getDate();
		// console.log(this.scheduleDate)
		if (today == this.scheduleDate) {
			this.isToday = true;
			this.dateName = "Today"
		}
		else if (this.scheduleDate - today == ONE_DAY) {
			this.isTomorrow = true;
			this.dateName = "Tomorrow"
		}
		else {
			this.dateName = "Next " + this.weekdays[ new Date(this.scheduleDate).getDay() ];
		}

		var _this = this;

		if (this.scheduleDate > today)
			this.isFutureDay = true;
		else 
			this.isFutureDay = false;
	}

	componentDidMount(){
		
		this.getUpdatedSchedule();
	}

	shouldComponentUpdate(nextProps, nextState) {

		// 1. only update current and upcoming schedule days
		return (this.props.isCurrent || nextProps.isCurrent);
		

	}

	componentDidUpdate(){
		this.setDateVars();
		var _this = this;
		TweenMax.killDelayedCallsTo(_this.updateTime)

		if (this.props.slideLifecycle) 
			this.props.slideLifecycle(this.enter, this.exit)

		// 1. switch current schedule if __ minutes past last entry
		if (getCurrentDate(this.props.config) > (this.state.lastEntry + (ONE_MINUTE * this.props.config.stopQueueInMinutesBeforeEvent)))
			this.props.functions.switch( this.props.dayValue );

		// 2. only initialize scroll if the current schedule is a future day and it isn't already scrolling
		//    sets isScrolling to true in the is scrolling function
		if (this.props.isCurrent && this.isFutureDay && !this.state.isScrolling)
			this.scrollSchedule();

		// 3. when the currenty scrolling day because today, stop scrolling
		if (this.isToday && this.state.isScrolling) {
			this.stopScrollingSchedule();
		}

		// 4. update the clock
		var updateInterval = (this.props.config.isDev && this.props.config.speedUpTime)
			? 30 / this.props.config.speedUpTime
			: 30 ;
		TweenMax.delayedCall(updateInterval, _this.updateTime);	

	}

	stopScrollingSchedule = () => {
		var _this = this;
		TweenMax.killTweensOf(this.refs.tourList);
		
		
		$(_this.refs.tourListWrapper).removeClass("is-scrolling");
		$(_this.refs.tourListWrapper).removeClass("at-bottom");
		
		this.setState({isScrolling: false});
		TweenMax.to(this.refs.tourList, 1, {alpha: 0, onComplete: function() {
			//_this.setState({isScrolling: false});
			_this.fadeUnseenEntries();
			TweenMax.set(_this.refs.tourList, {top: 0});
			TweenMax.to(_this.refs.tourList, 1, {alpha: 1, delay: .2})
		}});
	}

	scrollSchedule = () => {

		var _this = this;
		TweenMax.killTweensOf(this.refs.tourList)
		$(this.refs.tourListWrapper).removeClass("is-scrolling");
		TweenMax.set(this.refs.tourList, {top: 0});

		// 1. calculate how much to scroll tour list
		var heightOfList = $(this.refs.tourList).height();
		var hieghtOfWrapper = $(this.refs.tourListWrapper).height();
		var scrollMove = heightOfList - hieghtOfWrapper;

		// 2. only scroll if contents is longer than wrapper
		if (scrollMove > 0) {

			// keep scroll speed consistent no matter how long the schedule
			var duration = scrollMove / 40;

			// scroll down
			var delay = 5;
			TweenMax.to(this.refs.tourList, duration, {top: -scrollMove, delay: delay, ease: Power0.easeNone,
				onStart: function() {
					$(_this.refs.tourListWrapper).addClass("is-scrolling");
				},
				onComplete: function() {
					$(_this.refs.tourListWrapper).addClass("at-bottom");
				}
			});

			// fade out when you reach the bottom
			delay = delay + duration + 5;
			TweenMax.to(this.refs.tourList, 1, {alpha: 0, delay: delay,
				onStart: function() {
					$(_this.refs.tourListWrapper).removeClass("at-bottom");
					$(_this.refs.tourListWrapper).removeClass("is-scrolling");
				},
				onComplete: function() {
					console.log("set top");
					TweenMax.set(_this.refs.tourList, {top: 0});
				}
			});

			// fade back in and start scroll over again
			delay = delay + 1;
			TweenMax.to(_this.refs.tourList, 1, {alpha: 1, delay: delay,
				onComplete: function() {
					_this.scrollSchedule();
				}				
			});
	
		}

		// 3. set that it's scrolling so it doesn't reinit this on update
		if (!this.state.isScrolling)
			this.setState({isScrolling: true})
	}

	getUpdatedSchedule = () => {
		var timestamps = Object.keys(this.props.schedule);

		timestamps.forEach(function(part, index, theArray) {
		  theArray[index] = parseInt( part );
		});

		var currentTimestamps = this.state.allTimestamps
		var timestampsAreTheSame = timestamps.length == currentTimestamps.length
			&& timestamps.every(function(v,i) { return v === currentTimestamps[i]})

		if ( timestampsAreTheSame )
			return;

		this.setState({
			allTimestamps: timestamps,
			lastEntry: timestamps[ timestamps.length - 1],
			currentTime: getCurrentTime(this.props.config)
		})
	}

	updateTime = () => {
		TweenMax.killDelayedCallsTo(this.updateTime)
		this.setState({ currentTime: getCurrentTime(this.props.config) });
	}


	enter = () => {
		this.updateTime();
		TweenMax.killTweensOf(this.refs.tourList);
		this.setState({isScrolling: false});

		if (this.isToday)
			this.fadeUnseenEntries();

	}

	exit = () => {

		console.log("exit");

	}

	fadeUnseenEntries = () => {
		if (this.props.functions.fadeUnseenEntries) {
			var firstTimestamp = Object.keys(this.props.schedule)[0];
			var firstEntry = this.refs["entry" + firstTimestamp].refs.component;
			this.props.functions.fadeUnseenEntries( firstEntry );
		}	
	}

	createScheduleEntries = (scheduleJSON) => {
		// console.log("CREATE")
		var scheduleEntryHTML = [];
		var currentDate = getCurrentDate(this.props.config);
		for (var index in scheduleJSON) {
			// 1. entry remains on screen until __ minutes after start time
			var hasPassed = index < (currentDate - (ONE_MINUTE * this.props.config.stopQueueInMinutesBeforeEvent));

			// 2. entry gets queuing alert __ mintues before start time and until __ minutes after it goes away
			var isQueuing = scheduleJSON[index].title == "Historic Site Visit"
				&& currentDate < (parseInt(index) + (ONE_MINUTE * this.props.config.stopQueueInMinutesBeforeEvent))
				&& currentDate > (parseInt(index) - (ONE_MINUTE * this.props.config.startQueueInMinutesBeforeEvent))

			scheduleEntryHTML.push(
				<ScheduleEntry
					key={ index }
					ref={ "entry" + index }
					sold_out={scheduleJSON[index].sold_out}
					low_availability={scheduleJSON[index].low_availability}
					data={ scheduleJSON[index] }
					hasPassed={ hasPassed }
					isQueuing={ isQueuing }
					functions={ this.props.functions }
				/>);
		}
		return scheduleEntryHTML;
	}
 
	render() {

		return (
			<div ref="component" className={"schedule" + (this.isFutureDay? " is-future-day" : "")}>
				<h1>
					<span className="title">Entry Times for { this.dateName }</span>
					<div ref="clock" className="clock">
						<div className="time">{ this.state.currentTime }</div>
						<div className="ampm">
							<div className={ "am " + (CurrentAMPM == "am" ? "active" : "")}>A.M.</div>
							<div className={ "pm " + (CurrentAMPM == "pm" ? "active" : "")}>P.M.</div>
						</div>
					</div>

				</h1>
				<div ref="tourListWrapper" className="tour-list-wrapper">
					<ul ref="tourList" className="tour-list">
						{ this.createScheduleEntries(this.props.schedule) }
					</ul>
				</div>
			</div>
		)
	}
}

export default Schedule;


