import React, { Component } from 'react';
import { TweenMax, Power0 } from 'gsap'



class Media extends Component {
	state = {
		error: false
	}
	componentDidMount() {
		if (this.refs.video)
			TweenMax.set(this.refs.video, {alpha: 0});
		if (this.refs.image)
			TweenMax.set(this.refs.image, {alpha: 0});
	}

	componentDidUpdate() {
		if (this.props.slideLifecycle != null)
			this.props.slideLifecycle(this.enter, this.exit)
	}

	startImageAnimation = (animationType) => {
		if (animationType == "none") return;
		var time = (this.props.animationTime) ? this.props.animationTime : 20;
		var delay = (this.props.animationDelay) ? this.props.animationDelay : 0;
		var image = this.refs.image;

		switch(animationType) {
			case "zoom-in":
				TweenMax.fromTo(image, time, {scale: 1}, {scale: 1.2, delay: delay});
				break;
			case "zoom-out":
				TweenMax.fromTo(image, time, {scale: 1.2}, {scale: 1, delay: delay});
				break;
			case "pan-right":
				TweenMax.fromTo(image, time, {objectPosition: "0% 50%"}, {objectPosition: "100% 50%", delay: delay, ease: Power0.easeNone});
				break;
			case "pan-left":
				TweenMax.fromTo(image, time, {objectPosition: "100% 50%"}, {objectPosition: "0% 50%", delay: delay, ease: Power0.easeNone});
				break;
			case "pan-up":
				TweenMax.fromTo(image, time, {objectPosition: "50% 100%"}, {objectPosition: "50% 0%", delay: delay, ease: Power0.easeNone});
				break;
			case "pan-down":
				TweenMax.fromTo(image, time, {objectPosition: "50% 0%"}, {objectPosition: "50% 100%", delay: delay, ease: Power0.easeNone});
				break;
			default:
				return;
		}
	}

	enter = () => {
		if (this.refs.video) {
			TweenMax.set(this.refs.video, {alpha: 0});
			this.refs.video.currentTime = 0;
			this.refs.video.play();
			TweenMax.to(this.refs.video, .8, {alpha: 1, delay: .2});
		}
		if (this.refs.image && this.props.animation) {
			this.startImageAnimation(this.props.animation)
		}
	}

	exit = () => {
		if (this.refs.video)
			this.refs.video.pause();
	}

	load = (event) => {
		TweenMax.to(event.target, 1, {alpha: 1});
	}

	error = (event) => {
		console.log(event.target.src);
		this.setState({ error: true });

	}
 
	render() {

		var classes = "";
		if (this.props.nocrop)
			classes += " no-crop";
		if (this.props.border)
			classes += " has-border";

		var figcaption = (this.props.credit) ? this.props.credit : "";
		figcaption += (this.props.caption) ? (" " + this.props.caption) : "";

		if (figcaption)
			classes += " has-caption";
		
		return (
			<figure ref="component" className={ classes }>
				<div className="border"></div>

				{ this.props.video && !this.state.error
					? <video ref="video" loop muted onLoad={ this.load } onError={ this.error }>
						<source src={ this.props.video.replace(this.props.config.assetDomain, "./uploads") } type="video/mp4"/>
						Your browser does not support the video tag.
					 </video>
					: null
				}

				{ this.props.image && !this.props.video && !this.state.error
					? <img
						ref="image"
						src={ this.props.image.replace(this.props.config.assetDomain, "./uploads") }
						alt=""
						onLoad={ this.load }
						onError={ this.error }/>
					: null
				}

				{ this.state.error || !this.props.image
					? <img ref="image" src={ this.props.config.imageDefault } alt="" onLoad={ this.load }/>
					: null
				}

				<figcaption ref="caption" dangerouslySetInnerHTML={{__html: figcaption}}></figcaption>
			</figure>
		)
	}
}

export default Media;


