import React, { Component, Fragment } from 'react';
import Slide from './components/Slide.js';
import Controls from './components/utility/Controls.js';
import { getCurrentDate, getDateWithoutTime, getTimeWithoutDate, ONE_MINUTE } from './libs/utility.js'
import { TweenMax } from "gsap";
import io from 'socket.io-client';
import $ from 'jquery'; 

let Schedule;
const Productions = require('./data/productions.json');
const Playlist = require('./data/playlists.json');

const socket = io('http://localhost:8000', {
  jsonp: false,
  transports: ['websocket'] // you need to explicitly tell it to use websockets
});

class App extends Component {
	state = {
		currentSlideIndex: -1,
		controlsAreVisible: false,
		config: {},
		scheduleJSON: {},
		playlistCollection: {},
		currentPlaylistID: -1,
		currentDayBundleID: -1,
		allPlaylists: {},
		allPlaylistTimes: {}
	}
	
	numberOfSlides = 0;
	productionTypes = {};
	
	constructor(props) {
		super(props);
		this.getConfig();
		this.getSchedule();
		
		// 1. using config variables, organize data feeds
		this.parseProductionToType(Productions);
		// this.parseRawSchedule(Schedule, true);
		this.parseRawPlaylists();
		this.parseRawPlaylistCollection();	
		var _self = this;

		this.checkScheduleIntervalInSeconds = this.state.config.checkScheduleIntervalInMinutes * 60 * 1000;
		//if(!this.state.config.isDev)
			this.setPullDownTimer();
	}
	
	setPullDownTimer(){
		
		clearTimeout(this.timer);
		this.timer = setTimeout(this.downloadContent, this.checkScheduleIntervalInSeconds);

	}
	downloadContent(){
		console.log("download Content")
		socket.emit('updateContent');
		
	}
	getConfig = () => {
		
		this.state.config = $.ajax({
			type: 'GET',
			url:`${process.env.PUBLIC_URL}/config.json`,
			async: false
		}).responseJSON;

		if (this.state.config.previewMode)
			this.state.controlsAreVisible = true;
	}

	getSchedule = () => {

		var scheduleURL = encodeURI("https://www.fords.org/ajax/feeds/schedule/?key=aCmBM8R6ZFBseaJGrec2dsjA4YdZkuQA");

		var url = (this.state.config.previewMode && window.location.href != "http://localhost:3000/")
			? window.location.href + "proxy.php"
			: `${process.env.PUBLIC_URL}/data/thisSchedule.json`;

		// Schedule = $.ajax({
		// 	type: 'GET',
		// 	url: url,
		// 	async: false
		// }).responseJSON;

		var _this = this;

		$.ajax({
			//dataType: "json",
			type: 'GET',
			url: url,
			async: true,
			success: function(data) {
				console.log("success");
				_this.parseRawSchedule(data, true);
			},
			error: function(e){
				console.log("error");
			  console.log(e)
			}
		});
	}

	parseProductionToType = (productions) => {
		for (var index in productions) {
			var title = productions[index].title;
			var type = productions[index].meta_type;
			this.productionTypes[ title ] = type;
		}
	}

	parseRawPlaylistCollection = () => {
		for (var index in Playlist.screens) {
			var screen = Playlist.screens[index];

			// 1. get the screen's playlist collection by screenID
			if (screen.screen_id == this.state.config.screenID) {
				// 2. then parse this collection into day bundles by timestamp
				for (var dayBundleIndex in screen.playlist_collections) {
					var dayBundle = screen.playlist_collections[ dayBundleIndex ];
					var timestamp = Date.parse( dayBundle.start_date );
					// 3. note that playlist_id is actually the collection id.
					//    the current collection can change as time passes

					//convert the timestamp to just the day
					var bundleDate = new Date(parseInt(timestamp));
					bundleDate.setTime(bundleDate.getTime() + bundleDate.getTimezoneOffset() * 60 * 1000);
					var bundleTimestamp = bundleDate.valueOf();

					this.state.playlistCollection[ bundleTimestamp ] = this.parseRawDayBundle(dayBundle.playlists);
					
				}
				return;
			}
		}

		
	}

	parseRawDayBundle = ( dayBundle ) => {
		// 1. map timestamp to playlist id
		var parseDayBundle = {};
		for (var index in dayBundle) {
			var id = dayBundle[index];
			var timestamp = this.state.allPlaylistTimes[ id ];
			parseDayBundle[ timestamp ] = id
		}

		return parseDayBundle;
	}

	parseRawPlaylists = () => {
		for (var index in Playlist.playlists) {
			var playlist = Playlist.playlists[index];
			//var timestamp = getFormattedTimeFromMilliseconds( playlist.playlist_start_time );
			this.state.allPlaylists[ playlist.playlist_id ] = {
				title: playlist.playlist_title,
				items: playlist.playlist_items
			}
			this.state.allPlaylistTimes[ playlist.playlist_id ] = playlist.playlist_start_time;
		}		
	}

	calculateCurrentPlaylist = () => {
		// 1. returns a boolean if the current playlist was changed

		// 2. get the current collection's timestamp
		var currentDayBundleTimestamp = Number.NEGATIVE_INFINITY;
		var maxDayBundleTimestamp = Object.keys(this.state.playlistCollection)[0];
		var currentDate = getCurrentDate(this.state.config);
		for (var timestamp in this.state.playlistCollection) {

			if (timestamp <= currentDate && timestamp > currentDayBundleTimestamp) {
				currentDayBundleTimestamp = timestamp;
			}
			if (timestamp > maxDayBundleTimestamp) {
				maxDayBundleTimestamp = timestamp;
			}
		}

		var currentDayBundleID = (this.state.playlistCollection[ currentDayBundleTimestamp ])
			? currentDayBundleTimestamp
			: maxDayBundleTimestamp;
		
		
		// 3. get the current playlist's ID
		
		var currentPlaylistTimestamp = 0;
		var maxPlaylistTimestamp = 0;
		var currentTime = getTimeWithoutDate( currentDate );
		var playlistsInDayBundle = this.state.playlistCollection[ currentDayBundleID ]
		for (var timestamp in playlistsInDayBundle) {
			timestamp = parseInt(timestamp);
			//console.log(new Date(currentTime), new Date(parseInt(timestamp)))
			if (timestamp <= currentTime && timestamp > currentPlaylistTimestamp) {
				currentPlaylistTimestamp = timestamp;
			}
			if (timestamp > maxPlaylistTimestamp) {

				maxPlaylistTimestamp = timestamp;
			}
		}

		// 4. if there is no timestamp before the current time, use the last playlist of the day
		var currentPlaylistID = (this.state.playlistCollection[ currentDayBundleID ][ currentPlaylistTimestamp ])
			? this.state.playlistCollection[ currentDayBundleID ][ currentPlaylistTimestamp ]
			: this.state.playlistCollection[ currentDayBundleID ][ maxPlaylistTimestamp ];
		

		// 5. set states
		if (this.state.currentDayBundleID !== currentDayBundleID || this.state.currentPlaylistID !== currentPlaylistID) {
			
			this.changePlaylist(currentPlaylistID, currentDayBundleID);
			return true;	
		}
		else
			return false;

	}

	changePlaylist = (playlistID, bundleID = -1) => {

		var _this = this;
		TweenMax.to(this.refs.component, .5, {alpha: 0, onComplete: function() {
			_this.setState({
				currentDayBundleID: bundleID,
				currentPlaylistID: playlistID,
				currentSlideIndex: -1
			})	

			_this.numberOfSlides = Object.keys( _this.state.allPlaylists[ playlistID ].items ).length;			
		}});

		TweenMax.to(this.refs.component, 1, {alpha: 1, delay: .5, onStart: function() {
			_this.setState({
				currentSlideIndex: _this.state.config.startingSlideIndex
			})				
		}});

	}


	componentDidMount() {
		this.calculateCurrentPlaylist();	

		//init socket listeners
		socket.on('connect', function () {
			console.log('connected!');
		});

		socket.on('respond', function (data) {
			console.log(data);
		});
		socket.on('contentUpdated', (data) => {
			//this.props.setDataReady(true);
			console.log('content ready');

			this.checkForChanges();
			this.setPullDownTimer();
		});			

	}

	checkForChanges(){
		var tempSchedule = $.ajax({
			type: 'GET',
			url:`${process.env.PUBLIC_URL}/data/thisSchedule.json`,
			async: false
		}).responseJSON;

		this.parseRawSchedule(tempSchedule);

	}

	//every time data, autoPlay,  or current slide changes
	componentDidUpdate() {
		TweenMax.killDelayedCallsTo(this.autoPlayNextSlide);
		this.numberOfSlides = Object.keys( this.state.allPlaylists[ this.state.currentPlaylistID ].items ).length;

		if (!this.state.config.autoPlay)
			return;


		var _this = this;

		// 1. get the current slide's slidetime
		var currentSlide = this.refs["slide" + this.state.currentSlideIndex];
		if (currentSlide) {
			var slideTime = currentSlide.slideTime;
			TweenMax.delayedCall(slideTime, _this.autoPlayNextSlide);
		}

	}

	autoPlayNextSlide = (slideTime) => {
		

		if (!this.state.config.autoPlay)
			return;


		// 1. first check if you should change playlists, if so change currentPlaylistID and currentSlide to -1
		//    do not turn on during preview mode or will change to the currentPlaylist instead of previewed playlist
		if (!this.state.config.previewMode) {
			var playlistChanged = this.calculateCurrentPlaylist();
			if (playlistChanged) return;
		}

		// 2. then change slide index, if currentSlide set to -1 in calculateCurrentPlaylists, it will start at 0
		var nextSlideIndex = (this.state.currentSlideIndex + 1) % this.numberOfSlides;
		this.setState({ currentSlideIndex: nextSlideIndex });			

	}

	parseRawSchedule = (array, firstTime) => {
		
		var now = getCurrentDate(this.state.config);
		var scheduleArray = {};
		for (var index in array) {
			
			// 1. only grab the first 200 entries
			if (index >= 200) break;

			// 2. do not include add-on type productions
			var productionType = this.productionTypes[ array[index].title ];
			if ( productionType !== "Add-on") {


				var entryDateAndTime = new Date( array[index].timestamp );
				var entryDate = getDateWithoutTime( entryDateAndTime )

				// 3. only include entries that haven't passed 
				if ( entryDateAndTime >= (now - (ONE_MINUTE * 30)) ) {

					// 4. if the day is not added to the object, create it
					if (!scheduleArray[ entryDate ])
						scheduleArray[ entryDate ] = {};


					var timestamp = new Date( array[index].timestamp ).valueOf();

					// 5. if there are 2 events at the same time, adda second to the timestamp
					//	  so they can both appear on the schedule
					timestamp = ( scheduleArray[ entryDate ][ timestamp ] )
						? timestamp + 1
						: timestamp;


					scheduleArray[ entryDate ][ timestamp ] = {
						title: array[index].title,
						price: array[index].price,
						duration: array[index].duration,
						time_formatted: array[index].time_formatted,
						attractions: array[index].attractions,
						sold_out: array[index].sold_out,
						low_availability: array[index].low_availability,
						entry_date:entryDate,
						timestamp:timestamp
					}
				}	
			}
			else console.log("filter out " + array[index].title);
		}

		if(firstTime){
			this.state.scheduleJSON = scheduleArray;
			console.log("first time", this.state.scheduleJSON);
			return;
		}

		var different = false;

		for(var cat in this.state.scheduleJSON){
			
			for(var item in this.state.scheduleJSON[cat]){
				

				if (!scheduleArray[cat]) {
					different = true;
					console.log("new schedule entry found", cat)					
					break;
				}

				var obj = this.state.scheduleJSON[cat][item];
				var newObj = scheduleArray[cat][item];

				if(!newObj){
					different = true;
					console.log("SOMETHING CHANGED", cat, item)
					break;
				}
				
				if(newObj.sold_out != obj.sold_out || newObj.low_availability != obj.low_availability){
					
					different = true;
					break;
				}
			}
		}

		if( different){
			console.log("state not the same - resetting state")
			this.setState({scheduleJSON: scheduleArray})
			//this.state['scheduleJSON'] = scheduleArray
		}else{
			console.log("ALL GOOOOOOD")
		}
	}

	slideNavigation = (event) => {
		//change slide based on key input
		switch (event.keyCode) {
			case 67 : //c - for controls
				this.setState({ controlsAreVisible: !this.state.controlsAreVisible});
				break;
			case 80 : //p - for pause and play
				var config = {...this.state.config }
				config.autoPlay = !config.autoPlay;
				this.setState({ config: config });
				break;
			case 37 : //left arrow
				var prevSlideIndex = ((this.state.currentSlideIndex + this.numberOfSlides) - 1) % this.numberOfSlides;
				this.setState({ currentSlideIndex: prevSlideIndex });
				break;
			case 39 : //right arrow
				var nextSlideIndex = (this.state.currentSlideIndex + 1) % this.numberOfSlides;
				this.setState({ currentSlideIndex: nextSlideIndex });
				break;
			default :
				return;
		}
	}

	focusOnNavigation = (event) => {
		//keeps navigation input in focus without scrolling
		var x = window.scrollX, y = window.scrollY;
		event.target.focus();
		window.scrollTo(x, y);

	}

	createSlides = (slidesJSON) => {

		var slidesHTML = [];

		var index = 0;
		for (var slideIndex in slidesJSON) {

			slidesHTML.push(
				<Slide
					key={ this.state.currentPlaylistID + "-" + slideIndex } // that way the key of every slide even between playlists is different
					ref={ "slide" + index }
					slideIndex={ index }
					currentSlideIndex={ this.state.currentSlideIndex }
					currentPlaylistID ={ this.state.currentPlaylistID }
					config={ this.state.config }
					schedule={ this.state.scheduleJSON }
					data={ slidesJSON[slideIndex] }
					autoPlayNextSlide={ this.autoPlayNextSlide }
				/>);

			index++;
		}
		return slidesHTML;
	}

	render() {

		var currentPlaylistData = this.state.allPlaylists[ this.state.currentPlaylistID ];
		return (
			<Fragment>
				<Controls
					allPlaylists={ this.state.allPlaylists }
					changePlaylist={ this.changePlaylist }
					currentPlaylistID ={ this.state.currentPlaylistID }
					isVisible={ this.state.controlsAreVisible } />

				<input type="text" id="navigation" autoFocus
						onKeyDown={ this.slideNavigation } 
						onBlur={ this.focusOnNavigation } />
				<div ref="component" className="App">



					{ this.state.scheduleJSON && currentPlaylistData
						? this.createSlides( currentPlaylistData.items ) 
						: ""
					}
				</div>
			</Fragment>
			
		
		);
	}
}

export default App;



