import React, { Component } from 'react';
//import logo from ();



class Footer extends Component {

 
	render() {

		var footerText = this.props.text
		? this.props.text
		: <span>For tickets, hours, and more, visit us online at <strong>Fords.org</strong>.</span>
		
		return (

			<footer className={ this.props.isLight ? "light" : "" }>
				<div className="logo">
					<object data={'./logo.svg'} type="image/svg+xml">Logo</object>
				</div>
				{ this.props.text
					? <div className="footer-text" dangerouslySetInnerHTML={{__html: this.props.text }}></div>
					: <div className="footer-text">
						<span>For tickets, hours, and more, visit us online at <strong>Fords.org</strong>.</span>
					  </div>

				}
				
				

			</footer>
		)
	}
}

export default Footer;


